<template>
  <div>
    <div class="shopcart">
      <div class="content">
        <div class="content-left" @click="toggleShow">
          <div class="logo-wrapper">
            <div class="logo" :class="{highlight: totalCount}">
              <i class="iconfont iconyidiandiantubiao01" :class="{highlight: totalCount}"></i>
            </div>
            <div class="num" v-if="totalCount">{{totalCount}}</div>
          </div>
          <div class="price" :class="{highlight: totalCount}">￥{{totalPrice}}</div>
          <div class="desc">另需配送费￥{{info.deliveryPrice}} 元</div>
        </div>
        <div class="content-right">
          <div class="pay" :class="payClass">
            {{payText}}
          </div>
        </div>
      </div>
      <transition name="move">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="clearCart">清空</span>
          </div>
          <div class="list-content">
            <ul>
              <li class="food" v-for="(food, index) in cartFoods" :key="index">
                <span class="name">{{food.name}}</span>
                <div class="price"><span>￥{{food.price}}</span></div>
                <div class="cartcontrol-wrapper">
                  <CartControls :food="food"></CartControls>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="list-mask" v-show="listShow"></div>
    </transition>
  </div>
</template>

<script>
import {mapState, mapGetters} from 'vuex'
import CartControls from '../CartControls/CartControls'
import BScroll from 'better-scroll'
import {MessageBox} from 'mint-ui'
export default {
  data () {
    return {
      isShow: false
    }
  },
  computed: {
    ...mapState(['info', 'cartFoods']),
    ...mapGetters(['totalCount', 'totalPrice']),
    payText () {
      const {totalPrice} = this
      const {minPrice} = this.info
      if (totalPrice === 0) {
        return `￥${minPrice}元起送`
      } else if (totalPrice < minPrice) {
        return `还差${minPrice - totalPrice}元起送`
      } else {
        return '去买单'
      }
    },
    payClass () {
      const {totalPrice} = this
      const {minPrice} = this.info
      return totalPrice >= minPrice ? 'enough' : 'not-enough'
    },
    listShow () {
      if (this.totalCount === 0) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.isShow = false
        return false
      }
      if (this.isShow) {
        this.$nextTick(() => {
          if (!this.scroll) {
            // eslint-disable-next-line vue/no-side-effects-in-computed-properties
            this.scroll = new BScroll('.list-content', {
              click: true
            })
          } else {
            this.scroll.refresh()
          }
        })
      }
      return this.isShow
    }
  },
  methods: {
    toggleShow () {
      if (this.totalCount > 0) {
        this.isShow = !this.isShow
      }
    },
    clearCart () {
      MessageBox.confirm('确认清空购物车?').then(action => {
        this.$store.dispatch('clearCart')
      }).catch(action => {

      })
    }
  },
  components: {
    CartControls
  }
}
</script>

<style lang="scss">
  @import "../../common/scss/mixin";
  .shopcart{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
    width: 100%;
    height: 48px;
    .content{
      display: flex;
      background: #141d27;
      font-size: 0;
      color: rgba(255,255,255,0.4);
      .content-left{
        flex: 1;
        .logo-wrapper{
          display: inline-block;
          vertical-align: top;
          position: relative;
          top: -10px;
          margin: 0 12px;
          padding: 6px;
          width: 56px;
          height: 56px;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          border-radius: 50%;
          background: #141d27;
          .logo{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            text-align: center;
            background: #2b343c;
            &.highlight{
              background: #02a774;
            }
            .iconyidiandiantubiao01{
              line-height: 44px;
              font-size: 24px;
              color: #80858a;
              &.highlight{
                color: #fff;
              }
            }
          }
          .num{
            position: absolute;
            top: 0;
            right: 0;
            width: 24px;
            height: 16px;
            line-height: 16px;
            text-align: center;
            border-radius: 16px;
            font-size: 9px;
            font-weight: 700;
            color: #fff;
            background: #f01414;
            -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
            box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
          }
        }
        .price{
          display: inline-block;
          vertical-align: top;
          margin-top: 5px;
          line-height: 24px;
          padding-right: 12px;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          font-size: 16px;
          font-weight: 700;
          color: #fff;
          &.highlight{
            color: #fff;
          }
        }
        .desc{
          display: inline-block;
          vertical-align: bottom;
          margin-bottom: 15px;
          margin-left: -45px;
          font-size: 10px;
        }
      }
      .content-right{
        flex: 0 0 105px;
        width: 105px;
        .pay{
          height: 48px;
          line-height: 48px;
          text-align: center;
          font-size: 12px;
          font-weight: 700;
          color: #fff;
          &.not-enough{
            background: #2b333b;
          }
          &.enough{
            background: #00b43c;
            color: #fff;
          }
        }
      }
    }
    .shopcart-list{
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      width: 100%;
      transform: translateY(-100%);
      &.move-enter-active,&.move-leave-active{
        transition: transform .3s;
      }
      &.move-enter,&.move-leave-to{
        transform: translateY(0)
      }
      .list-header{
        @include clearFix();
        height: 40px;
        line-height: 40px;
        padding: 0 18px;
        background: #f3f5f7;
        border-bottom: 1px solid rgba(7,17,27,0.1);
        .title{
          float: left;
          font-size: 14px;
          color: #07111b;
        }
        .empty{
          float: right;
          font-size: 12px;
          color: #00a0dc;
        }
      }
      .list-content{
        padding: 0 18px;
        max-height: 217px;
        overflow: hidden;
        background: #fff;
        .food{
          position: relative;
          padding: 12px 0;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          position: relative;
          border: none;
          @include bottom-border-1px(#e4e4e4);
          .name{
            line-height: 24px;
            font-size: 14px;
            color: #07111b;
          }
          .price{
            position: absolute;
            right: 90px;
            bottom: 12px;
            line-height: 24px;
            font-size: 14px;
            font-weight: 700;
            color: #f01414;
          }
          .cartcontrol-wrapper{
            position: absolute;
            right: 0;
            bottom: 6px;
          }
        }
      }
    }
  }
  .list-mask{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 40;
    backdrop-filter: blur(10px);
    opacity: 1;
    background: rgba(7,17,27,0.6);
    &.fade-enter-active,&.fade-leave-active{
      transition: opacity .3s;
    }
    &.fade-enter,&.fade-leave-to{
      opacity: 0;
    }
  }
</style>



